html {
  font-size: 62.5%;
  box-sizing: border-box;
  /* Hack para box model */
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 16px;
  /* 1rem = 10px */
  font-family: "Krub", sans-serif;
  background-color: #111;
  color: #fff;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

img {
  max-width: 100%;
}

/* Tipografia */
h1, h2, h3 {
  text-align: center;
  margin: 0.5rem 2rem;
}

h1 {
  font-size: 3.8rem;
  display: contents;
}

h2 {
  font-size: 2.8rem;
  padding-top: 4rem;
}

h3 {
  font-size: 1.8rem;
  margin-top: 2rem;
}

p {
  font-size: 2rem;
  margin: 0.5rem 1rem;
}

a {
  text-decoration: none;
}

.etiqueta {
  font-style: italic;
  font-size: 1.5rem;
  margin: 0;
  color: darkgray;
  width: 100%;
}

.boton {
  display: block;
  background-color: #0097a7;
  color: white;
  padding: 1rem 3rem;
  margin: 0 2rem;
  font-size: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 0.5rem;
  text-align: center;
  border: none;
}

.boton:hover {
  cursor: pointer;
}

.boton-secundario {
  background-color: #0097a7;
  color: white;
  padding: 0.5rem 1rem;
  margin-top: 2.5rem;
  margin-bottom: 0;
  font-size: 1.5rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 400;
  text-align: center;
  border: none;
}
@media (min-width: 768px) {
  .boton-secundario {
    margin-top: -3rem;
  }
}

.sombra {
  box-shadow: 5px 10px 5px 0px rgba(168, 168, 168, 0.5);
  background-color: white;
  padding: 2rem;
  border-radius: 1rem;
}

.w-sm-100 {
  width: 100%;
}
@media (min-width: 768px) {
  .w-sm-100 {
    width: auto;
  }
}

.flex {
  display: flex;
}

.alinear-derecha {
  justify-content: flex-end;
}

/* Titulo */
.titulo span {
  font-size: 2rem;
}

/* Navegacion */
.nav-bg {
  position: fixed;
  display: flex;
  justify-content: center;
  z-index: 2;
  top: 0;
  background-color: #111;
  width: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#111111+0,111111+100&1+0,0+100 */
  background: -moz-linear-gradient(top, #111111 0%, rgba(17, 17, 17, 0) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #111111 0%, rgba(17, 17, 17, 0) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #111111 0%, rgba(17, 17, 17, 0) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#111111", endColorstr="#00111111", GradientType=0);
  /* IE6-9 */
}

.navegacion-mobil {
  display: flex;
  flex-direction: column;
  text-align: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 2s ease-in-out, opacity 3s ease-in-out, height 0.3s linear;
}
.navegacion-mobil a {
  display: block;
  text-align: center;
  color: white;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 700;
  padding: 1rem;
}

.navegacion-principal {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-direction: row;
  align-items: flex-start;
  gap: 2rem;
  visibility: hidden;
  opacity: 0;
  transition: visibility 2s ease-in-out, opacity 3s ease-in-out;
}
.navegacion-principal.fijo {
  position: fixed;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-top: -14rem;
  background-color: #0097a7;
  box-shadow: 0 0.3rem 0.5rem;
}
.navegacion-principal.fijo .boton-inicio {
  grid-column: 1/5;
}
.navegacion-principal.fijo a {
  padding: 0.5rem;
}
@media (min-width: 768px) {
  .navegacion-principal.fijo {
    display: flex;
    padding: 1rem;
    width: 120rem;
    max-width: 95%;
    margin-left: max(calc((100% - 120rem) / 2), 2.5%);
    margin-bottom: 5rem;
  }
}

.navegacion-principal a {
  display: block;
  text-align: center;
  color: lightgray;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 700;
  padding: 1rem;
}

.navegacion-principal a:hover {
  background-color: #ffc107;
  color: black;
}

.hero {
  background-image: url(/build/img/hero.webp);
  background-position-y: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 45rem;
  margin-bottom: 2rem;
}

.contenido-hero {
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contenido-hero h2,
.contenido-hero p {
  display: flex;
  align-items: center;
  color: white;
}

@media (min-width: 768px) {
  .servicios {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: dense;
  }
}
@media (min-width: 768px) {
  .servicios .servicio-proximamente {
    grid-column: 3/4;
  }
}

.servicio {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.servicio h3 {
  color: #0097a7;
}

.servicios .iconos {
  height: 15rem;
  width: 15rem;
  background-color: #ffc107;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

@media (min-width: 768px) {
  .servicios .iconos-proximamente {
    height: 10rem;
    width: 10rem;
    margin-bottom: 2rem;
  }
}

.servicio p {
  line-height: 2rem;
  text-align: center;
}

.portfolio {
  background-color: #0097a7;
  margin-top: 2rem;
  padding: 3rem;
}
.portfolio h2 {
  color: white;
}

.proyectos {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .proyectos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}

.proyecto {
  background-color: white;
  padding-bottom: 3rem;
  margin-bottom: 3rem;
  border-radius: 1rem;
}
.proyecto h3 {
  padding-bottom: 2rem;
}
.proyecto .boton {
  margin: 1rem 3rem;
  width: 90%;
}

.amarillo {
  background-color: #ffc107;
  margin-bottom: 2rem;
}

.imagen-rock {
  height: 20rem;
  background-image: url(/build/img/imagen_vocalista.jpg);
  background-size: cover;
  background-position: center;
}

.imagen-cafe {
  height: 20rem;
  background-image: url(/build/img/banner.jpg);
  background-size: cover;
  background-position: center;
}

.ver-todas {
  display: flex;
  align-items: flex-end;
}

.blog h2 {
  margin-top: 5rem;
}

.entrada-blog {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.entrada-blog h3 {
  text-align: inherit;
}

.texto-entrada span {
  color: #0097a7;
}

.contacto {
  background-color: #757575;
  margin: 4rem auto;
  padding: 2rem;
  border-radius: 1rem;
}
.contacto h2 {
  margin-top: 2rrem;
  color: white;
}
.contacto .medios {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .contacto .medios {
    flex-direction: row;
    justify-content: space-around;
  }
}
.contacto .medio {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 18rem;
  width: 18rem;
}
.contacto a {
  width: 8rem;
  height: 8rem;
}
.contacto a:hover {
  width: 10rem;
  height: 10rem;
}
.contacto p {
  margin-top: 1rem;
  color: white;
}

.footer {
  text-align: center;
  background-color: lightgray;
  padding: 0.5rem 0;
}
.footer p {
  margin: 0;
}

.video {
  position: relative;
  height: 100vh;
  overflow: hidden;
  background-color: #111;
  display: flex;
  justify-content: center;
  align-items: center;
}
.video video {
  position: absolute;
  display: block;
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.mostrar {
  visibility: visible !important;
  opacity: 1 !important;
}

.contenido-video {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  transition: visibility 2s ease-in-out, opacity 3s ease-in-out;
}
.contenido-video h2 {
  padding-top: 0;
}
.contenido-video p {
  display: flex;
  align-items: center;
}

.barra-inversa {
  width: 100%;
  height: 5rem;
  position: absolute;
  bottom: 0;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#7db9e8+0,111111+100&0+0,1+100 */
  background: -moz-linear-gradient(top, rgba(125, 185, 232, 0) 0%, #111111 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(125, 185, 232, 0) 0%, #111111 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(125, 185, 232, 0) 0%, #111111 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#007db9e8", endColorstr="#111111",GradientType=0 );
  /* IE6-9 */
}

.mobil-menu {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  background-color: transparent;
  transition: visibility 2s ease-in-out, opacity 3s ease-in-out, background-color 3s ease-in-out;
}
@media (min-width: 768px) {
  .mobil-menu {
    display: none;
  }
}
.mobil-menu img {
  height: 5rem;
}

.desplegar {
  visibility: visible;
  opacity: 1;
  background-color: rgba(17, 17, 17, 0.5);
}

.ubicacion {
  display: flex;
  align-items: center;
}

@media (min-width: 768px) {
  .dos-columnas {
    display: grid;
    grid-template-columns: 40% auto;
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .dos-columnas img {
    align-self: center;
    margin-top: 2rem;
  }
}